.App {
  text-align: center;
}

input {
  margin: 4px;
}

.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

div {
  border: 4px thin black;
}

.title {
  text-decoration: underline;
  margin-right: 10px;
  text-align:center;
}

#cards {
  margin-bottom: 15px;
}

.indivCard {
  max-width: 250px;
  max-height: 250px;
  background-color: lightgray;
  border: 25px thin black;
  margin: 10px;
  text-align: center;
}

.cardsContainer {
  border: 5px thin black;
  display: flex;
  flex-wrap: wrap;
}